<template>
    <div class="feature">
        <div class="feature-c" @click="$emit('click')">
            <div class="feature-title"><span class="foverlay">{{ data.title }}</span></div>
            <img :src="data.img">
        </div>
    </div>
</template>

<script>
export default {
    name: "feature",
    props: {
        data: {
            type: Object,
            default: () => {
                return {
                    title: '分类',
                    img: '',
                    href: ''
                }
            }
        }
    }
}
</script>

<style scoped lang="scss">
.feature {
    position: relative;
    cursor: pointer;

    img {
        height: 160px;
        border-radius: 2px;
    }

    .feature-c:hover .foverlay {
        opacity: 1;
    }

    .foverlay {
        position: absolute;
        z-index: 2;
        height: 160px;
        text-align: center;
        line-height: 160px;
        background: rgba(21, 148, 198, 0.75);
        color: white;
        font-size: 16px;
        opacity: 0;
        border-radius: 2px;
        width: 230.3px;

        -moz-transition: opacity .4s ease-out;
        -o-transition: opacity .4s ease-out;
        -webkit-transition: opacity .4s ease-out;
        transition: opacity .4s ease-out;
    }
}
</style>